import React from 'react';
import styles from './Footer.module.scss';
import FooterList from "./FooterList";
import phone from '../../img/icons/footer-phone.svg'
import clock from '../../img/icons/clock.svg'
import location from '../../img/icons/footer-location.svg'
import mail from '../../img/icons/mail.svg'

const Footer = () => {
    return (
        <footer className={styles.footerBackground}>
            <div className={styles.footerContainer}>
                <section className={styles.footerFirstFloor}>
                    <FooterList title={''} list={['      ', ' ',
                    ' ', '   ', ' ']}/>
                    <FooterList title={' '} list={[' ', ' ', ' ']}/>
                    <FooterList title={''} list={[' ', '', '  ']}/>
                    <FooterList title={' '} list={[' ', ' ',' ', ' ']}/>
                </section>
                <section className={styles.footerFirstFloor}>
                    <FooterList title={''} list={['8 (800) 777-49-67', `- 7:00 - 16:00 ()`, ', . , . 1,  6', 'info@zlatmax.ru']} image = {[phone,clock,location,mail]}/>
                    <FooterList title={' '} list={['   ']}/>
                    <FooterList title={' '} list={['  ?     ' +
                    '  30    ,   ']}/>
                    <FooterList title={' '} list={['  !']}/>
                </section>
            </div>
        </footer>
    );
};

export default Footer;